import * as React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import ListItemText from '@material-ui/core/ListItemText';
import Avatar from '@material-ui/core/Avatar';
import IconButton from '@material-ui/core/IconButton';
import Grid from '@material-ui/core/Grid';
import FolderIcon from '@material-ui/icons/Folder';
import DeleteIcon from '@material-ui/icons/Delete';
import EditIcon from "@material-ui/icons/Edit"

import StackGrid from "react-stack-grid";
import firebase from "../firebase"
import { useState, useEffect } from "react"
import Favorites from './Favorites';
import Home from './Home';
import Folder from './Folder';

import { BrowserRouter as Router, 
  Link, Route, Switch, useRouteMatch, useParams } from 'react-router-dom';
import Header from '../components/Header';
import FormDialog from '../components/FormDialog';
import BottomNav from '../components/BottomNav';

import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';

import Divider from '@material-ui/core/Divider';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import CloseIcon from '@material-ui/icons/Close';
import Slide from '@material-ui/core/Slide';

import MenuIcon from '@material-ui/icons/Menu';
import AddIcon from '@material-ui/icons/Add';
import SearchIcon from '@material-ui/icons/Search';
import MoreIcon from '@material-ui/icons/MoreVert';
import ArrowBackIcon from '@material-ui/icons/ArrowBack';
import { Paper } from '@material-ui/core';

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
		width: `calc(100vw - ${theme.spacing(2)}px)`,
		marginLeft: theme.spacing(1),
		marginRight: theme.spacing(1),
		marginTop: theme.spacing(9),
    marginBottom: theme.spacing(16),
    zIndex: 0
  },
  title: {
    margin: theme.spacing(4, 0, 2),
  },
  bottomAppBar: {
    top: "auto",
    bottom: 0
  },
}));

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="left" ref={ref} {...props} />;
});

export default function Collection() {

  const [open, setOpen] = React.useState(false)
  const handleClickOpen = () => {
    setOpen(true)
  }

  const handleClose = () => {
    setOpen(false)
  }

  const handleLinkClick = (id, name) => {
    setFolderId(id)
    setFolderName(name)
    console.log(folderId)
    handleClickOpen()
  }
  
  const [folderId, setFolderId] = useState("")
  const [folderName, setFolderName] = useState("")
  const classes = useStyles()
  const folderData = useDetails()

  const collections = folderData.map(folder =>
    
      <Grid item key={folder.folder_id}>
        <Paper variant="outlined">
        <ListItem button onClick={e => handleLinkClick(folder.folder_id, folder.folder_name)}>
          <ListItemAvatar>
            <Avatar>
              <FolderIcon />
            </Avatar>
          </ListItemAvatar>
          <ListItemText
            primary={folder.folder_name}
            secondary={folder.folder_desc}
          />
          <ListItemSecondaryAction>
            <IconButton edge="end" aria-label="delete"
              onClick={e => deleteCollection(folder.folder_id)}>
              <DeleteIcon />
            </IconButton>
          </ListItemSecondaryAction>
        </ListItem>
        </Paper>
      </Grid>
    
  )

  return (
    <>
      <Header title={"Collection"} />
      <div className={classes.root}>
        <List>
          <StackGrid columnWidth="100%">
            {collections}
          </StackGrid>
        </List>
        <Dialog fullScreen open={open} TransitionComponent={Transition}>
          <Folder id={folderId} name={folderName} />
          <FormDialog mode={"link"} />
          <AppBar position="fixed" color="secondary" className={classes.bottomAppBar}>
            <Toolbar>
              <IconButton edge="start" color="inherit" aria-label="open drawer"
                onClick={handleClose}
              >
                <ArrowBackIcon />
              </IconButton>
              <IconButton color="inherit">
                <SearchIcon />
              </IconButton>
              <IconButton edge="end" color="inherit">
                <MoreIcon />
              </IconButton>
            </Toolbar>
          </AppBar>
        </Dialog>
      </div>
      <FormDialog mode={"collection"} />
    </>
  );
}

function deleteCollection(id) {

	firebase.firestore()
		.collection("collections")
		.doc(id)
		.delete()
		.then(() =>
			console.log("Success")
		).catch((err) =>
			console.error("Error removing document: ", err)
		)
}

function useDetails() {
	const [folderDetails, setFolderDetails] = useState([])

	useEffect(() => {
		const unsubscribe = firebase
			.firestore()
			.collection("collections")
			.onSnapshot((snapshot) => {
				const newDetails = snapshot.docs.map((doc) => ({
					folder_id: doc.id,
					...doc.data()
				}))
				setFolderDetails(newDetails)
			})
        
        return () => unsubscribe()
    }, [])
    
    return folderDetails
}